import FormControl from '@mui/material/FormControl';
import Input from '@mui/material/Input';
import InputLabel from '@mui/material/InputLabel';
import Typography from '@mui/material/Typography';
import IMask from 'imask';
import React, { useState } from 'react';
import { IMaskInput } from 'react-imask';
import { ReactElement } from 'react-imask/dist/mixin';
import './App.css';

const dateTimeformat = new Intl.DateTimeFormat('default', { hour: "2-digit", minute: "2-digit", hour12: false });

interface CustomProps {
  onChange: (event: { target: { value: string } }) => void;
}

const TextMaskCustom = React.forwardRef<ReactElement, CustomProps>(
  function TextMaskCustom(props, ref) {
    const { onChange, ...other } = props;
    return (
      <IMaskInput
        {...other}
        mask="hh:mm"
        blocks={{
          hh: {
            mask: IMask.MaskedRange,
            from: 0,
            to: 23,
          },
          mm: {
            mask: IMask.MaskedRange,
            from: 0,
            to: 59,
          }
        }}
        inputRef={ref}
        onAccept={(value: any) => onChange({ target: { value } })}
        overwrite
      />
    );
  },
);

const calculateResult = (time1: string, time2: string): string => {

  if (time1.length === 0) return "";
  if (time2.length === 0) time2 = "0";

  let h1 = 0;
  let min1 = 0;
  let h2 = 0;
  let min2 = 0;

  if (time1.includes(':')) {
    const part = time1.split(':');
    [h1, min1] = parseParts(part);
  } else {
    h1 = Number.parseInt(time1);
  }

  if (time2.includes(':')) {
    const part = time2.split(':');
    [h2, min2] = parseParts(part);
  } else {
    h2 = Number.parseInt(time2);
  }

  const fm = (min1 + min2) % 60;
  const fh = (h1 + h2 + Math.trunc((min1 + min2) / 60)) % 24;

  return `${String(fh).padStart(2, '0')}:${String(fm).padStart(2, '0')}`;


  function parseParts(part: string[]) {
    const h = Number.parseInt(part[0]);
    const m = part[1].length > 0 ? Number.parseInt(part[1]) : 0;
    return [h, m];
  }
}

function App() {

  const [startTime, setStartTime] = useState<string>(() => dateTimeformat.format(new Date()));
  const [duration, setDuration] = useState<string>("10:59");

  return (
    <div className="App">
      <FormControl variant="standard">
        <InputLabel htmlFor="start-time">Start time</InputLabel>
        <Input id="start-time" autoFocus onChange={e => setStartTime(e.target.value)} value={startTime} inputComponent={TextMaskCustom as any}></Input>
      </FormControl>
      <FormControl variant="standard">
        <InputLabel htmlFor="duration">Duration</InputLabel>
        <Input id="duration" onChange={e => setDuration(e.target.value)} value={duration} inputComponent={TextMaskCustom as any}></Input>
      </FormControl>
      <Typography variant="h1" component="h2">
        {calculateResult(startTime ?? "", duration ?? "")}
      </Typography>
    </div>
  );
}

export default App;
